import React, { Component } from "react";
import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';

class Slider extends Component {
    render() {
        const options = {
            margin: 8,
            responsiveClass: true,
            dots: true,
            autoplay: true,
            loop: true,
            nav:true,
            smartSpeed: 1500,
            responsive: {
                0: {
                    items: 1,
                },
                400: {
                    items: 1,
                },
                600: {
                    items: 1,
                },
                700: {
                    items: 1,
                },
                1000: {
                    items: 1,

                }
            },
        };
        return (
            <OwlCarousel items={1}
                className="owl-carousel"
                {...options} >
                <div className="ps-banner">
                    <div className="container-no-round">
                        <div className="ps-banner__block">
                            <div className="ps-banner__content">
                                <div className="ps-banner__btn-group">
                                </div>
                                <div className="ps-banner__thumnail ps-banner__fluid"><img className="ps-banner__image" src={require('../website/img/habib/Slider.png')} />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="ps-banner">
                    <div className="container-no-round">
                        <div className="ps-banner__block">
                            <div className="ps-banner__content">
                                <div className="ps-banner__btn-group">
                                </div>
                                <div className="ps-banner__thumnail ps-banner__fluid"><img className="ps-banner__image" src= {require('../website/img/habib/img1.webp')} />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="ps-banner">
                    <div className="container-no-round">
                        <div className="ps-banner__block">
                            <div className="ps-banner__content">
                                <div className="ps-banner__btn-group">
                                </div>
                                <div className="ps-banner__thumnail ps-banner__fluid"><img className="ps-banner__image" src= {require('../website/img/habib/img2.webp')} />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                {/* <div className="ps-banner__thumnail ps-banner__fluid"><img  className="ps-banner__image" src= {require('../website/img/habib/Slider.png')}/></div>  
             <div className="ps-banner__thumnail ps-banner__fluid"><img  className="ps-banner__image" src= {require('../website/img/habib/img1.webp')} /></div>  
             <div className="ps-banner__thumnail ps-banner__fluid"><img  className="ps-banner__image" src= {require('../website/img/habib/img2.webp')}/></div>   */}
            </OwlCarousel>
        );
    }
}

export default Slider;